import { makeId } from '../id';
import { isNumericString, isNumber, readParams } from './util';
import Item from './Item';


export function makeVectorClass(events) {
    return class MyVector {
        id;
        items;

        constructor(p1, p2, p3) { // ...params doesn't work here for some reason
            const params = [p1, p2, p3];
            const { size, options, items } = readParams(params, { size: 0, options: {}, items: [] });
            this.items = items ?? (size !== undefined ? new Array(size) : []);

            for ( let i = 0; i < size; i++ ) {
                this.items[i] = undefined;
            }

            if ( size === 0 && items.length === 0 ) {
                throw new Error("Vector must have either size or items");
            }


            this.id = makeId();
            this.name = options?.name;

            console.info("Created vector,", this.id);
            events.push({
                event: 'create',
                type: 'vector',
                componentId: this.id,
                name: options?.name,
                size,
                items: items.map(item => ({
                    value: item,
                    id: makeId()
                })),
                color: options?.color,
            });

            return new Proxy(this, {
                get(target, prop) {
                    if ( isNumber(prop) || isNumericString(prop) ) {
                        const index = Number(prop);
                        if ( index >= target.items.length ) {
                            throw new Error(`No such index: ${index}`);
                        }

                        return target.items[prop].value;
                    }
                },
                set(target, prop, value) {
                    if ( isNumber(prop) || isNumericString(prop) ) {
                        const index = Number(prop);
                        if ( index >= target.items.length ) {
                            throw new Error(`No such index: ${index}`);
                        }

                        target.items[index] = value;
                        events.push({
                            event: 'setindex',
                            componentId: target.id,
                            name: this.name,
                            index,
                            item: { value, id: makeId() },
                        });

                        return true;
                    }

                    return false;
                }
            });
        }
    };
}

export default function Vector({ name, items, limbo, color }) {
    return (
        <div className="vector" style={{ backgroundColor: color }}>
            <h2>{ name }</h2>
            <table>
                <tbody>
                    {
                        items.map((item, index) => (
                            <tr key={index}>
                                <td className="vector-index">
                                    {index}
                                </td>
                                <td className="vector-value">
                                    <Item key={item.id} value={item.value} id={item.id} />
                                </td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
        </div>
    );
};
