import { makeId } from './id';


export function reducer(state, action) {
    switch ( action.event ) {
        case 'clear':
            return {};

        case 'message':
            return state;

        case 'create': {
            return {
                ...state,
                [action.componentId]: {
                    type: action.type,
                    items: action.items ?? [], // Items is cloned when action is created.
                    color: action.color,
                    size: action.size,
                    name: action.name
                }
            }
        }
        case 'delete': {
            const newState = {
                ...state
            };
            delete newState[action.componentId];

            return newState;
        }
        case 'push_front':
            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    front: undefined,
                    items: [
                        action.item,
                        ...state[action.componentId].items
                    ]
                }
            }
        case 'pop_front': {
            const items = state[action.componentId].items;
            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    front: items[0],
                    items: items.slice(1)
                }
            }
        }
        case 'push_back':
            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    items: [
                        ...state[action.componentId].items,
                        action.item,
                    ]
                }
            }
        case 'pop_back': {
            const items = state[action.componentId].items;
            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    back: items[items.length - 1],
                    items: items.slice(0, items.length - 1)
                }
            }
        }
        case 'insert': {
            const sortedItems = [
                ...state[action.componentId].items,
                action.item
            ];
            sortedItems.sort(action.comparator);

            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    items: sortedItems,
                    insertedId: action.item.id
                }
            }
        }
        case 'remove_id': {
            const sortedItems = state[action.componentId].items.filter(item => item.id !== action.item.id);

            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    items: sortedItems,
                    removedId: action.item.id
                }
            }
        }

        case 'getindex': {
            return state;
        }

        case 'setindex': {
            const items = [ ...state[action.componentId].items ];
            items[action.index] = action.item;
            console.log("items:", items);
            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    items
                }
            };
        }

        case 'delindex': {
            const items = [ ...state[action.componentId].items ];
            const limbo = items[action.index];
            console.log("delete:", limbo, items);
            items[action.index] = { value: undefined, id: makeId() };

            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    limbo,
                    items
                }
            };
        }

        case 'getprop': {
            return state;
        }

        case 'setprop': {
            const obj = state[action.componentId].items;
            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    items: {
                        ...obj,
                        [action.prop]: action.item

                    }
                }
            }
        }

        case 'delprop': {
            const items = { ...state[action.componentId].items };
            const limbo = items[action.prop];
            delete items[action.prop];

            return {
                ...state,
                [action.componentId]: {
                    ...state[action.componentId],
                    limbo,
                    items
                }
            };
        }

        default:
            throw new Error(`Unknown action: ${action.event}`);
    }
}
