import { makeId } from '../id';
import { readParams } from './util';
import Item from './Item';
import LimboItem from './LimboItem';

function minSort(a, b) {
    return a - b;
}

function maxSort(a, b) {
    return b - a;
}

export function makeHeapClass(events) {
    return class Heap {
        id;
        items;
        constructor(p1, p2, p3) {
            const params = [p1, p2, p3];
            const { options, items, func: comparator, string: compStr } =
                readParams(params, { options: {}, items: [], func: null, string: null });

            this.id = makeId();
            this.name = options?.name;

            if ( compStr && comparator ) {
                throw new Error("Can't give both comparator function or 'min'/'max'");
            }

            if ( comparator ) {
                this.comparator = (a, b) => comparator(a.value, b.value)
            } else if ( compStr === "max" ) {
                this.comparator = (a, b) => maxSort(a.value, b.value)
            } else if ( compStr === "min" || compStr === null ) {
                this.comparator = (a, b) => minSort(a.value, b.value)
            } else {
                throw new Error(`Unknown comparison method: ${compStr}`);
            }

            this.items = items.map(item => ({ value: item, id: makeId() }));
            this.items.sort(this.comparator);

            console.info("Created heap,", this.id);
            events.push({
                event: 'create',
                type: 'heap',
                componentId: this.id,
                name: options?.name,
                items: this.items.slice(0),
                color: options?.color,
            });
        }

        insert(value) {
            const newItem = { value, id: makeId() };
            this.items.push(newItem);
            this.items.sort(this.comparator);
            events.push({
                event: 'insert',
                componentId: this.id,
                name: this.name,
                item: newItem,
                comparator: this.comparator
            });
        }

        extract() {
            const item = this.items.shift();
            events.push({
                event: 'pop_front',
                componentId: this.id,
                name: this.name,
                item,
            });
            return item.value;
        }

        front() {
            return this.items[0];
        }

        back() {
            return this.items[this.items.length - 1];
        }

        get length() {
            return this.items.length;
        }

    };
}

export default function Heap({ name, items, limbo, color, insertedId }) {
    return (
        <div className="heap" style={{ backgroundColor: color }}>
            <h2>{ name }</h2>
            <div>
                { limbo &&
                  <LimboItem key={limbo.id} value={limbo.value} id={limbo.id} />
                }
                {
                    items.map((item, index) => (
                        item.id === insertedId
                        ? <Item key={item.id} value={item.value} id={item.id} />
                        : <Item key={item.id} value={item.value} id={item.id} />
                    ))
                }
            </div>
        </div>
    );
};
