export default function Controls({ run, stepForward, stepBackward, position, numSteps }) {
    return (
        <div className="controls">
            <button onClick={run}>Run</button>
            {' '}
            <button onClick={stepForward} disabled={position === numSteps}>Step forward</button>
            {' '}
            <button onClick={stepBackward} disabled={position === 0}>Step backward</button>
            {' '}
            <span>{position + 1}/{numSteps + 1}</span>
        </div>
    );
};
